import { Component, h, Prop, State, Element } from '@stencil/core';

/**
 * @category Navigation
 * @description A tab content component for the tab selector.
 */

@Component({
    tag: 'at-tab-content',
    shadow: false,
})
export class AtTabContent {
    @Element() el: HTMLElement;

    /**
     * ID of the tab
     */
    @Prop() tab_id: string;

    /**
     * Determines if the tab content is active
     */
    @Prop({ reflect: true }) is_active: boolean = false;

    @State() isActive: boolean = false;

    private tabSelector: HTMLAtTabSelectorElement;

    componentWillLoad() {
        this.isActive = this.is_active;
    }

    async componentDidLoad() {
        this.tabSelector = this.el.closest('at-tab-selector');

        if (this.tabSelector) {
            const activeTab = await this.tabSelector.getActiveTab();
            this.setIsActive(activeTab);
            this.tabSelector.addEventListener(
                'atuiChange',
                this.updateActiveState,
            );
        }
    }

    disconnectedCallback() {
        if (this.tabSelector) {
            this.tabSelector.removeEventListener(
                'atuiChangeId',
                this.updateActiveState,
            );
        }
    }

    setIsActive(id: string) {
        this.isActive = id === this.tab_id;
    }

    updateActiveState = (event: Event) => {
        const selectedTabId = (event as CustomEvent).detail;
        this.isActive = selectedTabId === this.tab_id;
    };

    render() {
        return (
            <div
                class={`${this.isActive ? 'flex flex-col' : 'hidden'}`}
                role="tabpanel"
                id={`panel-${this.tab_id}`}
                aria-labelledby={`tab-${this.tab_id}`}
                tabIndex={this.isActive ? 0 : -1}
                aria-hidden={!this.isActive}
            >
                <slot />
            </div>
        );
    }
}
